
const api={
    inforAddclickApi:function(vm,value){
        vm.$emit("inforAddCall",value);
        vm.$validator.errors.clear();//清除表单错误
    },
    ObjectCopy:objectcopy
    
}
function objectcopy(obj){
    var data={};
    for(var key in obj){
        if(typeof obj[key]==Object){
            objectcopy(obj[key]);
        }else{
            data[key]=obj[key];
        }
    }
    return  data;
}
export default api;


